.header-background
    background-color: #000

.header
    width: 90%
    margin: 0 auto
    font-family: 'Roboto'

.header-content
    display: flex
    width: 100%
    justify-content: space-between
    align-items: center

.header-logo
    img
        height: 10vh
        aspect-ratio: 1/1

.header-nav
    text-align: center
    display: flex
    align-items: center
    justify-content: space-between
    width: 30%
    div
        a
            text-decoration: none
            font-style: normal
            font-weight: 400
            font-size: 20px
            line-height: 150%
            text-align: center
            color: #FFFFFF
            cursor: pointer

.header-name
    h2
        font-style: normal
        font-weight: 700
        font-size: 32px
        line-height: 140%
        color: #FFFFFF

.header-hr
    color: #fff
    margin: 0
